import React from 'react';
import { Collapse } from 'antd';
import Container from 'components/UI/Container/Container';
import FAQWrapper from './FAQ.style';

const { Panel } = Collapse;

function callback(key) {
    console.log(key);
  }
  
const text = `
    A dog is a type of domesticated animal.
    Known for its loyalty and faithfulness,
    it can be found as a welcome guest in many households across the world.
`;

const FAQ = () => {
  return (
    <FAQWrapper>
      <h1 className="faq_title">FREQUENTLY ASKED<br /> QUESTIONS</h1>
      <Container fluid={true}>
        <div className="faqs">
          <Collapse onChange={callback} className="collapse">
            <Panel header="What is Premier Vacations?" key="1">
                <p>Premier Vacations is a digital collection of NFTs (non-fungible tokens).<br></br>
                Premier Vacations is the first collection of NFTs that represent REAL WORLD ASSETS.<br></br>
                Poised to take over the NFT Vacations for the greater good, the Bounty Staking and Reflection Rewards backed by REAL WORLD Assets make us unique and different from any other NFT community today.<br></br> The value of the community is reflected back to the community members. See Bounty Staking and Reflection Rewards below.</p>
            </Panel>
            <Panel header="When is the launch date?" key="2">
                <p> The Premier Vacations will take on its first mission on July 1, 2023. </p>
            </Panel>
            <Panel header="What are the reflection rewards?" key="3">
                <p> When an Premier NFT is minted, the commitment to the community is that 10% of the proceeds from the purchase will go to the Liquidity pool.<br></br>
                    Premier will provide a monthly report of the total assets. <br></br>
                    Through the revenue generated by the assets, a yield is realized, reflecting back to the NFT holders and members of the Premier community.<br></br>
                    The yield is paid out directly to the community through reflection rewards via a smart contract. </p>
            </Panel>
            <Panel header="What is cryptocurrency?" key="4">
                <p>
                    Cryptocurrency is decentralized digital money that’s based on blockchain technology. <br></br>
                    You may be familiar with the most popular versions, Bitcoin and Ethereum, but there are more than 5,000 different cryptocurrencies in circulation. <br></br>
                    Learn more at Forbes - <br></br>
                    <a href="https://www.forbes.com/advisor/investing/what-is-cryptocurrency/" target="blank"> forbes.com/advisor/investing/what-is-cryptocurrency/ </a>
                </p>
            </Panel>
            <Panel header="What is the blockchain?" key="5">
                <p> Blockchain is a system of recording information in a way that makes it difficult or impossible to change, hack, or cheat the system. <br></br>
                    A blockchain is essentially a digital ledger of transactions that is duplicated and distributed across the entire network of computer systems on the blockchain.<br></br>
                    Learn more at Investopedia - <br></br>
                    <a href="https://www.investopedia.com/terms/b/blockchain.asp" target="blank"> https://www.investopedia.com/terms/b/blockchain.asp </a> </p>
            </Panel>
            <Panel header="What is a Non-Fungible Token(NFT)?" key="6">
                <p>
                    A non-fungible token is a unique and non-interchangeable unit of data stored on a digital ledger.<br></br>
                    NFTs can be used to represent easily-reproducible items such as photos, videos, audio, and other types of digital files as unique items, and use blockchain technology to establish a verified and public proof of ownership.<br></br>
                    Learn more at CNN - <br></br>
                    <a href="https://www.cnn.com/2021/03/17/business/what-is-nft-meaning-fe-series/index.html" target="blank"> https://www.cnn.com/2021/03/17/business/what-is-nft-meaning-fe-series/index.html </a>
                </p>
            </Panel>
            <Panel header="What is Ethereum?" key="7">
                <p>
                    Ethereum is a platform powered by blockchain technology that is best known for its native cryptocurrency, called ether, or ETH, or simply ethereum. <br></br>
                    The distributed nature of blockchain technology is what makes the Ethereum platform secure, and that security enables ETH to accrue value. <br></br>                    
                    Learn more at Investopedia - <br></br>
                    <a href="https://www.investopedia.com/terms/e/ethereum.asp" target="blank">https://www.investopedia.com/terms/e/ethereum.asp</a> 
                </p>
            </Panel>
            <Panel header="How do I get a crypto wallet?" key="8">
                <p>
                    A crypto wallet refers to a virtual currency wallet that is accessible online, and it facilitates cryptocurrency transactions between the owner and end-users. <br></br>
                    A collection of private keys stored on a program connected to the internet is used to store and send different currencies such as Ethereum, Bitcoin, etc.<br></br>
                    There are two wallets you will need. <br></br>
                    One to purchase the cryptocurrency ETH (we recommend Coinbase) and the other to purchase the NFT (we recommend Metamask).
                </p>
            </Panel>
          </Collapse>
          <Collapse onChange={callback} className="collapse">
            <Panel header="How do I buy Ethereum?" key="9">
                <p> 
                    There are many secure options to purchase Ethereum: <br></br>
                    Coinbase - <a href="https://www.coinbase.com/" target="blank">https://www.coinbase.com/</a> <br></br>
                    Gemini - <a href="https://www.gemini.com/" target="blank">https://www.gemini.com/</a> <br></br>
                    Crypto.com - <a href="https://crypto.com/" target="blank">https://crypto.com/</a> <br></br>
                    Learn more at Time -  <br></br>
                    <a href="https://time.com/nextadvisor/investing/cryptocurrency/how-to-buy-ethereum/" target="blank">https://time.com/nextadvisor/investing/cryptocurrency/how-to-buy-ethereum/</a>
                </p>
            </Panel>
            <Panel header="How do I get a metamask account?" key="10">
                <p>
                    MetaMask is a web browser extension that allows you to manage your Ethereum private keys via your web browser. <br></br>
                    By doing so, it serves as a wallet for Ether and ERC-20 tokens, and allows you to visit the distributed web of tomorrow in your browser today. <br></br>
                    To be more specific, it allows you to run Ethereum Dapps (Decentralized Apps) right in your browser without running a full Ethereum node. (In other words, you don't have to download and sync the full blockchain on your device or computer). <br></br>
                    Our mission is to make Ethereum as easy to use for as many people as possible. <br></br>
                    What is MetaMask - <a href="https://youtu.be/YVgfHZMFFFQ" target="blank"> https://youtu.be/YVgfHZMFFFQ </a> <br></br>
                    How to install MetaMask on all devices and browsers: <br></br>
                    <a href="https://metamask.zendesk.com/hc/en-us/articles/360015489531-Getting-started-with-MetaMask" target="blank"> https://metamask.zendesk.com/hc/en-us/articles/360015489531-Getting-started-with-MetaMask </a>
                </p>
            </Panel>
            <Panel header="How do I transfer Ethereum to my metamask account?" key="11">
                <p>
                    From Coinbase and Gemini <br></br>
                    <a href="https://docs.everbloom.co/article/255jk54yda-step-three-sending-your-eth-to-your-walletM" target="blank"> https://docs.everbloom.co/article/255jk54yda-step-three-sending-your-eth-to-your-walletM </a> <br></br>
                    Coinbase to Metamask desktop - <br></br>
                    <a href="https://www.youtube.com/watch?v=A_j9feqBgnc" target="blank"> https://www.youtube.com/watch?v=A_j9feqBgnc </a> <br></br>
                    Coinbase to Metamask phone - <br></br>
                    <a href="https://www.youtube.com/watch?v=UtMdutkCoQI" target="blank"> https://www.youtube.com/watch?v=UtMdutkCoQI </a> <br></br>
                    Crypto.com to Metamask phone - <br></br>
                    <a href="https://www.youtube.com/watch?v=osqMhR6bt_Y" target="blank"> https://www.youtube.com/watch?v=osqMhR6bt_Y </a> <br></br>
                    <a href="https://www.youtube.com/watch?v=6NsX7jTvt3k" target="blank"> https://www.youtube.com/watch?v=6NsX7jTvt3k </a> <br></br>
                </p>
            </Panel>
            <Panel header="How do I purchase a PREMIER LUXURY?" key="12">
                <p>
                    Watch the video below on how to mint your PREMEIR NFT! Welcome to the Premier community! <br></br>
                    <a href="https://www.youtube.com/watch?v=I1QcdiE3ej0" target="blank"> https://www.youtube.com/watch?v=I1QcdiE3ej0 </a>
                </p>
            </Panel>
            <Panel header="How many mintable NFTs are there?" key="13">
                <p> Premier Vacations is a digital collection of unique NFTs (non-fungible tokens) that will be minted in multiple releases. <br></br>
                    Check the roadmap for the release dates. </p>
            </Panel>
            <Panel header="How and when will my reflection rewards be paid?" key="14">
                <p> All available reflection rewards will be paid out when available. <br></br>
                    PREMIER Tokens will be sent to your MetaMask wallet per each NFT. <br></br>
                </p>
            </Panel>
            <Panel header="How do I track my rewards?" key="15">
                <p> 
                    You can check your metamask wallet to check rewards paid to the wallet.
                </p>
            </Panel>
            <Panel header="Can I purchase or sell a PREMIER LUXURY on Opensea?" key="16">
                <p>
                    Once a person purchases the PREMIER NFT, all future transactions happen on a secondary market, such as Opensea.com. <br></br>

                    Opensea is one of the largest peer-to-peer marketplace for cryptogoods (like an eBay for crypto assets), which include collectibles, gaming items, and other virtual goods backed by a blockchain. <br></br>
                    
                    On Opensea, anyone can buy or sell these items through a smart contract.
                </p>
            </Panel>
          </Collapse>
        </div>      
      </Container>    
    </FAQWrapper>
  );
};

export default FAQ;
