import React, { useState } from 'react';
import Container from 'components/UI/Container/Container';
import { Button } from 'antd';
import OurStoryWrapper from './OurStory.style';


const FAQ = () => {
  
    const [readmoreStatus, setReadmoreStats] = useState(false)
    const handleReadMore = () => {
        let status = readmoreStatus;
        setReadmoreStats(!status);
    }
  
    return (
    <OurStoryWrapper id="about">
        <Container fluid={true}>
            <div className="banner">
                Invest in Real World Assets and generate up to <span className="code">20%</span> yearly return on the investment.
            </div>
            <div className="headBox box">                
                <h1>
                    Our Story
                </h1>
                <p className="stories">
                    At Premier, we're all about making memories and having fun. As a company incorporated in the state of Florida, we've been renting out vacation properties and cars since 2019.
                </p>
                
                
                {readmoreStatus && 
                    <div>
                        <p className="stories">
                            Our accommodations are designed to put a smile on your face without breaking the bank. For those vacationing in Orlando, Florida, we offer rental cars to make getting around a breeze. 
                            Check out our vacation properties at <a href="https://www.mypremiervacationsllc.com" target="blank"> www.mypremiervacationsllc.com </a> and our rental cars at <a href="https://turo.com/us/en/drivers/9170089" target="blank"> https://turo.com/us/en/drivers/9170089 </a>.                            
                        </p>
                        <p className="stories">
                            But that's not all we're about. Premier is unique in the world of crypto because we offer something that most projects don't: utility. We believe in the future of blockchain and DeFi, but we also believe that the current system isn't sustainable. Too many projects rely on new money to pay the current holders.
                        </p>
                        <p className="stories">
                            That's where NFTs come in. With NFTs, anyone in the world can have ownership of assets and verify that ownership on the blockchain. No more barriers to entry, no more closed doors. And that's why we've decided to bring our assets to the world of NFTs. We're starting with our cars, and splitting them into fractions through fractional ownership. Each NFT represents a piece of a real-world asset, whether it's a car, boat, plane, or real estate.
                        </p>                        
                    </div>
                }

                <Button className="read_more_button" onClick={handleReadMore}>
                    {!readmoreStatus && 'Read More'}
                    {readmoreStatus && 'Read Less'}
                </Button>
            </div>            
        </Container>    
    </OurStoryWrapper>
  );
};

export default FAQ;
