import React from 'react';
import { Collapse, Col, Row } from 'antd';
import Container from 'components/UI/Container/Container';
import MemberWrapper from './Member.style';

const Member = () => {
  return (
    <MemberWrapper>      
      <Container fluid={true}>
        <Row gutter={30} style={{ marginTop: 30 }}>
            <Col xl={12}>
                <h1>MEMBERS	OF	THE	PREMIER	COMMUNITY</h1>
                <p>
                  The	Premier	Community	members	take	advantage	of;
                </p>
                <p>
                  1. Earn rewards directly sent to your NFT Holders wallet.
                </p>
                <p>
                  2. Own exclusive NFTs representing fractions of Real World Assets such as planes, cars and real estate.	                  
                </p>
                <p>
                  3. Experience a unique architecture that ensures the Total Asset Value keeps growing over time.
                </p>
                <p>
                  4. Trade your NFTs on the secondary market through Opensea NFT, unlocking new opportunities.
                </p>
                <p>
                  Join us in revolutionizing the way we perceive and own assets, embracing the power of the blockchain technology. 
                </p>
                <p>
                  Be as a part of our Premier Community and embark on an exciting journey where digital ownership meets real-world utility.
                </p>
                <p>
                  Don't miss out an this groundbreaking opportunity!
                </p>                
            </Col>
            <Col xl={10} className="communityImagea">
                <img src="images/home_section2.png"></img>
            </Col>            
        </Row>
      </Container>    
    </MemberWrapper>
  );
};

export default Member;
